import utlis from '../../static/js/utlis.js'
export default {
	data() {
		return {
			type: 1,
			price: 10
		}
	},
	methods: {
		clickCharge(e) {
			this.type = e.target.dataset.type;
			this.price = e.target.dataset.price;
			if (e.target.dataset.type == 4) {
				this.price = 0
			}
		},
		onKeyInput: function(event) {
			this.price = event.target.value
		},
		chargeOver() {
			let self = this;
			uni.login({
				provider: 'weixin',
				success: function(res) {
					console.log(res);
					let data = {
						chargeAmt: self.price,//0.01
						chargeType: 10,
						custName: uni.getStorageSync('custName'),
						payType: 4000,
						wxCode: res.code
					}
					uni.showLoading({
						title: '加载中',
						mask: true
					})
					utlis.http("POST", 'topupWallet', data, function(res) {
						if (res.status == "success") {
							console.log(res)
							uni.hideLoading()
							// 仅作为示例，非真实参数信息。
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: res.wx2Json.timestamp,
								nonceStr: res.wx2Json.noncestr,
								package: res.wx2Json.package,
								signType: 'MD5',
								paySign: res.wx2Json.sign,
								success: function(res) {
									uni.showToast({
										title: '支付成功！',
										duration: 2000,
										mask: true,
										icon: 'success'
									})
									console.log('success:' + JSON.stringify(res));
								},
								fail: function(err) {
									uni.showToast({
										title: '支付失败！',
										duration: 2000,
										mask: true,
										icon: 'none'
									})
									console.log('fail:' + JSON.stringify(err));
								}
							})
						} else {
							uni.hideLoading()
							uni.showToast({
								title: res.msg,
								duration: 2000,
								mask: true,
								icon: 'none'
							})
						}
					})
				}
			});
		}
	}
}
